import React from 'react'
import Slider from 'react-slick'
import './Slider.scss'
import 'slick-carousel/slick/slick.css'
import 'slick-carousel/slick/slick-theme.css'
import sliderNike from '../../image/nike-poster.jpeg'
import sliderAdidas from '../../image/adidas-poster.jpeg'
import sliderPuma from '../../image/puma-poster.webp'

function SimpleSlider() {
  const settings = {
    arrows: true, //  
    infinite: true, //  
    speed: 500, //  
    slidesToShow: 1, //   
    slidesToScroll: 1, //   
  }

  return (
    <Slider {...settings} className='slider'>
      <div>
        <img src={sliderNike} alt=' ' />
      </div>
      <div>
        <img src={sliderAdidas} alt=' ' />
      </div>
      <div>
        <img src={sliderPuma} alt=' ' />
      </div>
    </Slider>
  )
}

export default SimpleSlider
